body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #85C1E9;
}

.App{
  background: #5DADE2;
  margin: 20px auto;
  width: 90%;
  max-width: 1000px;
  color: #eee;
  box-shadow: 1px 1px 15px #273746;
  border-radius:15px;
  
}

.navbar{
  padding: 10px 20px;
  text-align: center;
  background: #2E86C1;
  border-radius:15px 15px 0 0;
}

.navbar h1{
  margin: 10px 0;
}

.task-list{
  margin: 20px;
}

.task-list ul{
  padding: 0;
  list-style-type: none;
}

.task-list li{
  background: #2E86C1;
  border-radius: 10px;
  padding: 10px;
  cursor: pointer;
  margin: 10px 0;
}

.task-list li:hover{
  opacity: 0.7;
  text-decoration: line-through;
}

.task-list .title{
  font-weight: bold;
  color: #fff;
  font-size: 1.1em;
}

.empty{
  margin: 20px;
  text-align: center;
}

form{
  padding: 20px;
}

input[type='text']{
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  margin: 6px 0;
  background: #F4F6F7;
  color: #2E86C1;
  border: 0;
  border-radius:8px;
  font-weight: bold;
}

input[type='submit']{
  margin: 10px auto;
  background: #f02f66;
  border: 0;
  padding: 10px 20px;
  display: block;
  border-radius:8px;
  color: white;
  font-weight: bold;
  box-shadow: 1px 1px 5px #273746;
}


.footer{

  text-align: center;
  padding-bottom: 1px;
  font-size: small;
}

.footer a{
  text-decoration: none;
  color:white;
}